import {useLazy} from "../../../hooks/useLazy";

import styles from "./HomeAdvertisement.module.scss"
import {homeAdvertisement1,homeAdvertisement2,homeAdvertisement3} from "../../../assets/images"


function HomeAdvertisement() {
    const {isInView: inViewState1, ref: ref1} = useLazy(0.2,true)
    const {isInView: inViewState2, ref: ref2} = useLazy(0.5,true)
    const {isInView: inViewState3, ref: ref3} = useLazy(0.7,true)

    const textMobileActiveClass = inViewState2 ? styles['homeAdvertisement__text_activeMob'] : ''
    const imgMobileActiveClass = inViewState2 ? styles["homeAdvertisement__img_activeMob"] : ''
    return (
        <section className={styles['homeAdvertisement']}>
            <div className={`${styles["homeAdvertisement__container"]} container`} >
                <div className={`${styles["homeAdvertisement__texts"]} ${inViewState2 ? styles["homeAdvertisement__texts_active"] : ''}`}>
                    <h2 className={`${styles["homeAdvertisement__text"]} ${inViewState1 ? styles['homeAdvertisement__text_active'] : ''} ${textMobileActiveClass}`}>Build</h2>
                    <h2 className={`${styles["homeAdvertisement__text"]} ${inViewState1 ? styles['homeAdvertisement__text_active'] : ''} ${textMobileActiveClass}`}>Your</h2>
                    <h2 className={`${styles["homeAdvertisement__text"]} ${inViewState2 ? styles['homeAdvertisement__text_active'] : ''} ${textMobileActiveClass} lightBlueText`}>website</h2>
                    <h2 className={`${styles["homeAdvertisement__text"]} ${inViewState3 ? styles['homeAdvertisement__text_active'] : ''} ${textMobileActiveClass}`}>how do</h2>
                    <h2 className={`${styles["homeAdvertisement__text"]} ${inViewState3 ? styles['homeAdvertisement__text_active'] : ''} ${textMobileActiveClass}`}>you want</h2>
                </div>
                <div className={styles["homeAdvertisement__images"]}>
                    <img ref={ref1} src={homeAdvertisement1} alt="website" className={`${styles["homeAdvertisement__img"]} ${ inViewState1 ? styles["homeAdvertisement__img_active"] : ''} ${imgMobileActiveClass}`}/>
                    <img ref={ref2} src={homeAdvertisement2} alt="website" className={`${styles["homeAdvertisement__img"]} ${ inViewState2 ? styles["homeAdvertisement__img_active"] : ''} ${imgMobileActiveClass}`}/>
                    <img ref={ref3} src={homeAdvertisement3} alt="website" className={`${styles["homeAdvertisement__img"]} ${inViewState3 ?  styles["homeAdvertisement__img_active"] : ''} ${imgMobileActiveClass}`}/>
                </div>
            </div>
        </section>
    );
}

export default HomeAdvertisement;